import { Dialog } from 'vant'

export default {
  methods: {
    isUserLogin () {
      if (!this.$store.getters.ifToken) {
        Dialog.confirm({
          message: 'цВиш┐Шц▓бцЬЙчЩ╗х╜Х',
          confirmButtonText: 'хО╗чЩ╗х╜Х',
          cancelButtonText: 'хЖНщАЫщАЫ'
        })
          .then(() => {
            this.$router.push({
              path: '/login',
              query: {
                backUrl: this.$route.fullPath
              }
            })
          })
          .catch(() => {})
        return false
      } else {
        return true
      }
    }
  }
}
