<template>
  <div class="prodetail">
    <van-nav-bar fixed>
      <template #left>
        <div class="nav-back" @click="$router.back()">
          <van-icon name="arrow-left" color="#fff" />
        </div>
      </template>
    </van-nav-bar>

    <van-swipe :autoplay="3000" @change="onChange">
      <!-- onChangeщ╗ШшодхПВцХ░: indexф╗гшбих╜УхЙНщб╡чЪДч┤вх╝Х -->
      <van-swipe-item v-for="(image, index) in imagesSwipe" :key="index">
        <img v-lazy="image.external_url" />
      </van-swipe-item>

      <template #indicator>
        <div class="custom-indicator">
          {{ currentImgIndex + 1 }} / {{ imagesSwipe.length }}
        </div>
      </template>
    </van-swipe>

    <!-- хХЖхУБшп┤цШО -->
    <div class="info">
      <div class="title">
        <div class="price">
          <span class="now">я┐е{{ detail.goods_price_min }}</span>
          <span class="oldprice">я┐е{{ detail.line_price_max }}</span>
        </div>
        <div class="sellcount">х╖▓хФо{{ detail.goods_sales }}ф╗╢</div>
      </div>
      <div class="msg text-ellipsis-2">
        {{ detail.goods_name }}
      </div>

      <div class="service" @click="showService = true">
        <div class="left-words">
          <span><van-icon name="passed" />ф╕ГхдйцЧачРЖчФ▒щААш┤з</span>
          <span><van-icon name="passed" />48х░ПцЧ╢хПСш┤з</span>
        </div>
        <div class="right-icon">
          <van-icon name="arrow" />
        </div>
      </div>

      <!-- х╝╣хЗ║хХЖхУБф┐ЭщЪЬцЬНхКб -->
      <van-action-sheet
        v-model="showService"
        closeable
        round
        title="хХЖхУБф┐ЭщЪЬцЬНхКб"
      >
        <van-cell
          v-for="(item, index) in goodsService"
          :key="index"
          :label="item.summary"
        >
          <template #title>
            <van-icon name="passed" color="#fa2209"></van-icon>
            <span>{{ item.name }}</span>
          </template>
        </van-cell>
      </van-action-sheet>
    </div>

    <!-- хХЖхУБшпДф╗╖ -->
    <div class="comment">
      <div class="comment-title">
        <div class="left">хХЖхУБшпДф╗╖ ({{ totalComments }}цЭб)</div>
        <div class="right" @click="$router.push(`/comment?goodsId=${goodsId}`)">
          цЯечЬЛцЫ┤хдЪ <van-icon name="arrow" />
        </div>
      </div>
      <div class="comment-list">
        <div
          class="comment-item"
          v-for="(item, index) in commentsList"
          :key="index"
        >
          <div class="top">
            <img :src="item.user.avatar_url || defaultAvatar" alt="" />
            <div class="name">{{ item.user.nick_name }}</div>
            <van-rate
              :size="16"
              :value="scoreRate(item.score)"
              color="#ffd21e"
              void-icon="star"
              void-color="#eee"
            />
          </div>
          <div class="content">
            {{ item.content }}
          </div>
          <div class="time">
            {{ item.create_time }}
          </div>
        </div>
      </div>
    </div>

    <!-- хХЖхУБцППш┐░ -->
    <div class="desc" v-html="detail.content"></div>

    <!-- х║ХщГи -->
    <div class="footer">
      <div class="icon-home">
        <van-icon name="shop-o" @click="$router.push({path: '/'})" />
        <span>щжЦщб╡</span>
      </div>
      <div class="icon-cart">
        <van-icon
          name="shopping-cart-o"
          :badge="cartTotal || null"
          @click="$router.push('/cart')"
        />
        <span>ш┤нчЙйш╜ж</span>
      </div>
      <div class="btn-add" @click="ifShow = true; mode='cart'">хКахЕеш┤нчЙйш╜ж</div>
      <div
        class="btn-buy"
        @click="
          ifShow = true;
          mode = 'buyNow';
        "
      >
        члЛхИ╗ш┤нф╣░
      </div>
    </div>

    <!-- хКахЕеш┤нчЙйш╜ж члЛхН│ш┤нф╣░х╝╣хЗ║ -->
    <van-action-sheet
      v-model="ifShow"
      :title="mode === 'cart' ? 'хКахЕеш┤нчЙйш╜ж' : 'члЛхН│ш┤нф╣░'"
    >
      <div class="product">
        <div class="product-title">
          <div class="left">
            <img :src="detail.goods_image" alt="" />
          </div>
          <div class="right">
            <div class="price">
              <span>┬е</span>
              <span class="nowprice">{{ detail.goods_price_min }}</span>
            </div>
            <div class="count">
              <span>х║УхнШ</span>
              <span>{{ detail.stock_total }}</span>
            </div>
          </div>
        </div>
        <div class="num-box">
          <span>цХ░щЗП</span>
          <CountBox v-model="goodsNum"></CountBox>
        </div>
        <div class="showbtn" v-if="detail.stock_total > 0">
          <div class="btn" v-if="mode === 'cart'" @click="addToCart">
            хКахЕеш┤нчЙйш╜ж
          </div>
          <div class="btn now" v-else @click="buyNow">члЛхИ╗ш┤нф╣░</div>
        </div>
        <div class="btn-none" v-else>шпехХЖхУБх╖▓цКвхоМ</div>
      </div>
    </van-action-sheet>
  </div>
</template>

<script>
import CountBox from '@/components/CountBox.vue'
import { getProDetailApi, getCommentsApi, getGoodsServiceApi } from '@/api/goodsDetail'
import { addToCartApi, getCartTotalApi } from '@/api/cart'
import defaultAvatar from '@/assets/default-avatar.png'
import userLogin from '@/mixins/userLogin'

export default {

  mixins: [userLogin],

  components: {
    CountBox
  },

  name: 'DetailIndex',

  created () {
    console.log('createdшзжхПС')
    this.getCartTotal()
    this.getProDetail()
    this.getComments()
    this.getGoodsService()
  },

  activated () {
    console.log('activatedшзжхПС')
    this.getCartTotal()
    this.ifShow = false
    this.showService = false
    if (this.$route.params.goodsId !== this.goodsId) {
      this.goodsId = this.$route.params.goodsId
      this.currentImgIndex = 0
      this.goodsNum = 1
      this.getProDetail()
      this.getComments()
      this.getGoodsService()
    }
  },

  data () {
    return {
      goodsId: this.$route.params.goodsId,
      goodsSkuId: '',

      detail: {},
      imagesSwipe: [],
      currentImgIndex: 0,
      totalComments: '',
      commentsList: [],
      defaultAvatar,

      // хКахЕеш┤нчЙйш╜ж члЛхН│ш┤нф╣░
      ifShow: false,
      mode: '',
      // cart; buyNow

      // шжБш┤нф╣░чЪДхХЖхУБцХ░
      goodsNum: 1,

      cartTotal: 0,

      goodsService: [],
      showService: false
    }
  },

  methods: {
    onChange (index) {
      this.currentImgIndex = index
    },

    // шО╖хПЦхХЖхУБшпжцГЕ
    async getProDetail () {
      const { data: { detail } } = await getProDetailApi(this.goodsId)
      this.detail = detail
      this.imagesSwipe = detail.goods_images
      this.goodsSkuId = this.detail.skuList[0].goods_sku_id
      document.title = this.detail.goods_name
      // console.log(detail)
    },

    // шО╖хПЦшпДшо║
    async getComments () {
      const { data: { list, total } } = await getCommentsApi(this.goodsId)
      this.commentsList = list
      this.totalComments = total
      // console.log(list, total)
    },

    // хКахЕеш┤нчЙйш╜ж
    async addToCart () {
      if (!this.isUserLogin()) {
        return
      }
      const { data: { cartTotal } } = await addToCartApi(this.goodsId, this.goodsNum, this.goodsSkuId)
      this.cartTotal = cartTotal
      this.ifShow = false
    },

    // члЛхН│ш┤нф╣░
    buyNow () {
      this.$router.push({
        path: '/checkout',
        query: {
          mode: 'buyNow',
          goodsId: this.goodsId,
          goodsNum: this.goodsNum,
          goodsSkuId: this.goodsSkuId
        }
      })
    },

    // щб╡щЭвхИЭхзЛхМЦцЧ╢цШ╛чд║ш┤нчЙйш╜жхХЖхУБцА╗цХ░
    async getCartTotal () {
      const { data: { cartTotal } } = await getCartTotalApi()
      this.cartTotal = cartTotal
    },

    // хХЖхУБф┐ЭщЪЬцЬНхКб
    async getGoodsService () {
      const { data: { list } } = await getGoodsServiceApi(this.goodsId)
      this.goodsService = list
    },

    // шпДф╗╖цШЯцХ░
    scoreRate (score) {
      if (score === 10) {
        return 5
      } else if (score === 20) {
        return 3
      } else if (score === 30) {
        return 1
      } else {
        return 0
      }
    }
  }

}
</script>

<style lang="less" scoped>
.van-nav-bar {
  background-color: transparent;
}
.van-hairline--bottom::after {
  border: none;
}
.nav-back {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #666;
  width: 30px;
  height: 30px;
  border-radius: 30px;
  // margin: 7px 0 0 5px;
}
.prodetail {
  ::v-deep .van-icon-arrow-left {
    color: #333;
  }
  img {
    display: block;
    width: 100%;
  }
  .custom-indicator {
    position: absolute;
    right: 10px;
    bottom: 10px;
    padding: 5px 10px;
    font-size: 12px;
    background: rgba(0, 0, 0, 0.1);
    border-radius: 15px;
  }
  .desc {
    width: 100%;
    overflow: scroll;
    ::v-deep img {
      display: block;
      width: 100% !important;
    }
  }
  .info {
    padding: 10px;
  }
  .title {
    display: flex;
    justify-content: space-between;
    .now {
      color: #fa2209;
      font-size: 20px;
    }
    .oldprice {
      color: #959595;
      font-size: 16px;
      text-decoration: line-through;
      margin-left: 5px;
    }
    .sellcount {
      color: #959595;
      font-size: 16px;
      position: relative;
      top: 4px;
    }
  }
  .msg {
    font-size: 16px;
    line-height: 24px;
    margin-top: 5px;
  }
  .service {
    display: flex;
    justify-content: space-between;
    line-height: 40px;
    margin-top: 10px;
    font-size: 16px;
    background-color: #fafafa;
    .left-words {
      span {
        margin-right: 10px;
      }
      .van-icon {
        margin-right: 4px;
        color: #fa2209;
      }
    }
  }

  .comment {
    padding: 10px;
  }
  .comment-title {
    display: flex;
    justify-content: space-between;
    .right {
      color: #959595;
    }
  }

  .comment-item {
    font-size: 16px;
    line-height: 30px;
    .top {
      height: 30px;
      display: flex;
      align-items: center;
      margin-top: 20px;
      img {
        width: 20px;
        height: 20px;
      }
      .name {
        margin: 0 10px;
      }
    }
    .time {
      color: #999;
    }
  }

  .footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 55px;
    background-color: #fff;
    border-top: 1px solid #ccc;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    .icon-home,
    .icon-cart {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      font-size: 14px;
      .van-icon {
        font-size: 24px;
      }
    }
    .btn-add,
    .btn-buy {
      height: 36px;
      line-height: 36px;
      width: 120px;
      border-radius: 18px;
      background-color: #ffa900;
      text-align: center;
      color: #fff;
      font-size: 14px;
    }
    .btn-buy {
      background-color: #fe5630;
    }
  }
}

.tips {
  padding: 10px;
}

.product {
  .product-title {
    display: flex;
    .left {
      img {
        width: 90px;
        height: 90px;
      }
      margin: 10px;
    }
    .right {
      flex: 1;
      padding: 10px;
      .price {
        font-size: 14px;
        color: #fe560a;
        .nowprice {
          font-size: 24px;
          margin: 0 5px;
        }
      }
    }
  }

  .num-box {
    display: flex;
    justify-content: space-between;
    padding: 10px;
    align-items: center;
  }

  .btn,
  .btn-none {
    height: 40px;
    line-height: 40px;
    margin: 20px;
    border-radius: 20px;
    text-align: center;
    color: rgb(255, 255, 255);
    background-color: rgb(255, 148, 2);
  }
  .btn.now {
    background-color: #fe5630;
  }
  .btn-none {
    background-color: #cccccc;
  }
}
</style>
