<template>
  <div class="count-box" @click.stop>
    <div class="count-btns">
      <button
        @click="handleSub"
        :class="{ disabledColor: value <= 1, minus: true }"
      >
        -
      </button>
      <div @click="toShowPopup" class="inp">{{ value }}</div>
      <button @click="handleAdd" class="add">+</button>
    </div>

    <van-popup v-model="isShowPopup">
      <van-field type="digit" v-model="userInputCount" autofocus></van-field>
      <div class="btn" @click="confirmChange">чбошод</div>
    </van-popup>
  </div>
</template>

<script>
import { Toast } from 'vant'
export default {
  name: 'CountBox',

  props: {
    value: {
      type: Number,
      required: true
    }
  },

  data () {
    return {
      isShowPopup: false,
      userInputCount: ''
    }
  },

  methods: {
    toShowPopup () {
      this.isShowPopup = true
      this.userInputCount = this.value
    },

    handleSub () {
      const n = this.value - 1
      if (n <= 0) {
        Toast('шпехоЭш┤Эф╕НшГ╜хЖНхЗПх░Сф║ЖхСж~')
      } else {
        this.$emit('input', n)
      }
    },

    handleAdd () {
      this.$emit('input', this.value + 1)
    },

    confirmChange () {
      const n = +this.userInputCount
      if (n === this.value) {
        this.isShowPopup = false
        return
      }
      if (n <= 0) {
        Toast('цХ░щЗПф╜Оф║ОцЬАх░ПхА╝')
      } else {
        this.$emit('input', n)
      }
      this.isShowPopup = false
    }
  }

}
</script>

<style lang="less" scoped>
.count-btns {
  width: 92px;
  display: flex;
  justify-content: space-between;
  .add,
  .minus {
    width: 30px;
    height: 30px;
    outline: none;
    border: none;
    background-color: #efefef;
  }
  .inp {
    display: inline-block;
    width: 30px;
    height: 30px;
    outline: none;
    border: none;
    margin: 0 1px;
    background-color: #efefef;
    text-align: center;
    line-height: 30px;
  }
}
.disabledColor {
  color: #ccc;
}
.btn {
  height: 40px;
  line-height: 40px;
  margin: 20px;
  border-radius: 20px;
  text-align: center;
  color: rgb(255, 255, 255);
  background-color: rgb(255, 148, 2);
}
</style>
